@import url(https://fonts.googleapis.com/css?family=Montserrat:300,400,500,60,700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Roboto:300,400,500&display=swap);
@import url(https://fonts.googleapis.com/css?family=Montserrat:300,400,500,60,700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Roboto:300,400,500&display=swap);
@import url(https://fonts.googleapis.com/css?family=Montserrat:300,400,500,60,700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Roboto:300,400,500&display=swap);
.clearfix::after {
  content: "";
  clear: both;
  display: table;
}
* {
  font-family: 'Montserrat', sans-serif;
  color: #FFFFFF;
  box-sizing: border-box;
}
div.top-nav {
  display: flex;
  padding: 1.4rem;
  background-color: #2a2837;
}
div.top-nav .top-nav-item {
  padding-right: 1.6rem;
  padding-left: 1.6rem;
}
div.top-nav .top-nav-item > a {
  text-decoration: none;
  text-transform: uppercase;
  font-family: 'Montserrat', sans-serif;
  font-weight: 700;
  letter-spacing: 3px;
  font-size: 0.8rem;
}
div.top-nav .top-nav-item > a.Contact {
  border: 2px solid white;
  padding: 0.4rem;
}
@media only screen and (max-width: 900px) {
  div.top-nav {
    padding: 1rem;
    flex-direction: column;
  }
  div.top-nav .top-nav-item {
    padding-right: 0.8rem;
    padding-left: 0.8rem;
    padding-bottom: 0.8rem;
  }
  div.top-nav .top-nav-item > a.Contact {
    border: 1.5px solid white;
    padding: 0.3rem;
  }
}

.clearfix::after {
  content: "";
  clear: both;
  display: table;
}
* {
  font-family: 'Montserrat', sans-serif;
  color: #FFFFFF;
  box-sizing: border-box;
}
div.main-intro {
  background-color: #12131e;
}
div.main-intro img.image-main {
  max-width: 35%;
}
div.main-intro .main-intro-about {
  width: 65%;
  display: inline-block;
  padding: 2rem;
}
div.main-intro .main-intro-about > img {
  max-width: 30%;
  vertical-align: middle;
  padding: 2rem;
}
div.main-intro .main-intro-about > .about-text {
  display: inline-block;
  vertical-align: middle;
  max-width: 70%;
  padding-left: 2rem;
}
div.main-intro .main-intro-about .about-name {
  font-size: 2.8rem;
  font-family: 'Montserrat', sans-serif;
  font-weight: 700;
  margin-bottom: 1rem;
  text-transform: uppercase;
  letter-spacing: 5px;
  padding-bottom: 1rem;
}
div.main-intro .main-intro-about .about-general,
div.main-intro .main-intro-about .about-description {
  line-height: 1.6rem;
  font-family: 'Roboto', sans-serif;
  letter-spacing: 1px;
  font-size: 1rem;
  font-weight: 400;
  margin-right: 1rem;
}
div.main-intro .main-intro-about .about-description {
  margin-bottom: 1rem;
}
div.main-intro::after {
  content: "";
  clear: both;
  display: table;
}
@media only screen and (max-width: 1300px) {
  div.main-intro .main-intro-about > img {
    max-width: 30%;
    padding: 0;
  }
  div.main-intro .main-intro-about > .about-text {
    max-width: 70%;
  }
  div.main-intro .main-intro-about .about-name {
    font-size: 2.2rem;
  }
  div.main-intro .main-intro-about .about-general,
  div.main-intro .main-intro-about .about-description {
    line-height: 1.2rem;
    font-size: 0.8rem;
  }
}
@media only screen and (max-width: 1000px) {
  div.main-intro img.image-main {
    max-width: 100%;
  }
  div.main-intro .main-intro-about {
    width: 100%;
  }
  div.main-intro .main-intro-about > img {
    padding: 2rem;
  }
}
@media only screen and (max-width: 650px) {
  div.main-intro .main-intro-about {
    padding: 1rem;
  }
  div.main-intro .main-intro-about > img {
    max-width: 100%;
    display: block;
    margin: auto;
    padding: 15%;
  }
  div.main-intro .main-intro-about > .about-text {
    display: block;
    max-width: 100%;
  }
  div.main-intro .main-intro-about .about-name {
    font-size: 1.6rem;
  }
}

.clearfix::after {
  content: "";
  clear: both;
  display: table;
}
* {
  font-family: 'Montserrat', sans-serif;
  color: #FFFFFF;
  box-sizing: border-box;
}
.projects {
  background-color: #caa1c7;
  padding: 2rem;
  font-family: 'Roboto', sans-serif;
  font-weight: 600;
}
.projects .project-item {
  text-align: center;
  width: calc(100% / 3);
  display: inline-block;
  vertical-align: top;
  padding: 1rem;
  line-height: 1.2rem;
  font-size: 1rem;
}
.projects .project-item .project-title {
  text-transform: uppercase;
  font-family: 'Montserrat', sans-serif;
  font-weight: 700;
  letter-spacing: 2px;
  border: 3px solid #2a2837;
  padding: 0.4rem;
  display: inherit;
  margin-bottom: 1rem;
  color: #2a2837;
}
.projects .project-item > img {
  max-width: 100%;
  display: block;
  margin: auto;
}
.projects .project-item .project-desc {
  color: #2a2837;
  padding-top: 1rem;
  letter-spacing: 0.2px;
}
.projects .project-item .project-link {
  font-weight: 500;
}
.projects .project-item .project-link > a {
  display: block;
  text-decoration: none;
}
.projects .project-item .project-link a:active,
.projects .project-item .project-link a:focus {
  outline: 0;
  border: none;
  outline-style: none;
}
.projects::after {
  content: "";
  clear: both;
  display: table;
}
@media only screen and (max-width: 900px) {
  .projects > .project-item {
    width: 100%;
  }
}

/* http://meyerweb.com/eric/tools/css/reset/
   v2.0-modified | 20110126
   License: none (public domain)
*/
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
/* make sure to set some focus styles for accessibility */
:focus {
  outline: 0;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: '';
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  -webkit-appearance: none;
  -moz-appearance: none;
}
input[type=search] {
  -webkit-appearance: none;
  -moz-appearance: none;
  box-sizing: content-box;
}
textarea {
  overflow: auto;
  vertical-align: top;
  resize: vertical;
}
/**
 * Correct `inline-block` display not defined in IE 6/7/8/9 and Firefox 3.
 */
audio,
canvas,
video {
  display: inline-block;
  *display: inline;
  *zoom: 1;
  max-width: 100%;
}
/**
 * Prevent modern browsers from displaying `audio` without controls.
 * Remove excess height in iOS 5 devices.
 */
audio:not([controls]) {
  display: none;
  height: 0;
}
/**
 * Address styling not present in IE 7/8/9, Firefox 3, and Safari 4.
 * Known issue: no IE 6 support.
 */
[hidden] {
  display: none;
}
/**
 * 1. Correct text resizing oddly in IE 6/7 when body `font-size` is set using
 *    `em` units.
 * 2. Prevent iOS text size adjust after orientation change, without disabling
 *    user zoom.
 */
html {
  font-size: 100%;
  /* 1 */
  -webkit-text-size-adjust: 100%;
  /* 2 */
  -ms-text-size-adjust: 100%;
  /* 2 */
}
/**
 * Address `outline` inconsistency between Chrome and other browsers.
 */
a:focus {
  outline: thin dotted;
}
/**
 * Improve readability when focused and also mouse hovered in all browsers.
 */
a:active,
a:hover {
  outline: 0;
}
/**
 * 1. Remove border when inside `a` element in IE 6/7/8/9 and Firefox 3.
 * 2. Improve image quality when scaled in IE 7.
 */
img {
  border: 0;
  /* 1 */
  -ms-interpolation-mode: bicubic;
  /* 2 */
}
/**
 * Address margin not present in IE 6/7/8/9, Safari 5, and Opera 11.
 */
figure {
  margin: 0;
}
/**
 * Correct margin displayed oddly in IE 6/7.
 */
form {
  margin: 0;
}
/**
 * Define consistent border, margin, and padding.
 */
fieldset {
  border: 1px solid #c0c0c0;
  margin: 0 2px;
  padding: 0.35em 0.625em 0.75em;
}
/**
 * 1. Correct color not being inherited in IE 6/7/8/9.
 * 2. Correct text not wrapping in Firefox 3.
 * 3. Correct alignment displayed oddly in IE 6/7.
 */
legend {
  border: 0;
  /* 1 */
  padding: 0;
  white-space: normal;
  /* 2 */
  *margin-left: -7px;
  /* 3 */
}
/**
 * 1. Correct font size not being inherited in all browsers.
 * 2. Address margins set differently in IE 6/7, Firefox 3+, Safari 5,
 *    and Chrome.
 * 3. Improve appearance and consistency in all browsers.
 */
button,
input,
select,
textarea {
  font-size: 100%;
  /* 1 */
  margin: 0;
  /* 2 */
  vertical-align: baseline;
  /* 3 */
  *vertical-align: middle;
  /* 3 */
}
/**
 * Address Firefox 3+ setting `line-height` on `input` using `!important` in
 * the UA stylesheet.
 */
button,
input {
  line-height: normal;
}
/**
 * Address inconsistent `text-transform` inheritance for `button` and `select`.
 * All other form control elements do not inherit `text-transform` values.
 * Correct `button` style inheritance in Chrome, Safari 5+, and IE 6+.
 * Correct `select` style inheritance in Firefox 4+ and Opera.
 */
button,
select {
  text-transform: none;
}
/**
 * 1. Avoid the WebKit bug in Android 4.0.* where (2) destroys native `audio`
 *    and `video` controls.
 * 2. Correct inability to style clickable `input` types in iOS.
 * 3. Improve usability and consistency of cursor style between image-type
 *    `input` and others.
 * 4. Remove inner spacing in IE 7 without affecting normal text inputs.
 *    Known issue: inner spacing remains in IE 6.
 */
button,
html input[type="button"],
input[type="reset"],
input[type="submit"] {
  -webkit-appearance: button;
  /* 2 */
  cursor: pointer;
  /* 3 */
  *overflow: visible;
  /* 4 */
}
/**
 * Re-set default cursor for disabled elements.
 */
button[disabled],
html input[disabled] {
  cursor: default;
}
/**
 * 1. Address box sizing set to content-box in IE 8/9.
 * 2. Remove excess padding in IE 8/9.
 * 3. Remove excess padding in IE 7.
 *    Known issue: excess padding remains in IE 6.
 */
input[type="checkbox"],
input[type="radio"] {
  box-sizing: border-box;
  /* 1 */
  padding: 0;
  /* 2 */
  *height: 13px;
  /* 3 */
  *width: 13px;
  /* 3 */
}
/**
 * 1. Address `appearance` set to `searchfield` in Safari 5 and Chrome.
 * 2. Address `box-sizing` set to `border-box` in Safari 5 and Chrome
 *    (include `-moz` to future-proof).
 */
input[type="search"] {
  -webkit-appearance: textfield;
  /* 1 */
  /* 2 */
  box-sizing: content-box;
}
/**
 * Remove inner padding and search cancel button in Safari 5 and Chrome
 * on OS X.
 */
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none;
}
/**
 * Remove inner padding and border in Firefox 3+.
 */
button::-moz-focus-inner,
input::-moz-focus-inner {
  border: 0;
  padding: 0;
}
/**
 * 1. Remove default vertical scrollbar in IE 6/7/8/9.
 * 2. Improve readability and alignment in all browsers.
 */
textarea {
  overflow: auto;
  /* 1 */
  vertical-align: top;
  /* 2 */
}
/**
 * Remove most spacing between table cells.
 */
table {
  border-collapse: collapse;
  border-spacing: 0;
}
html,
button,
input,
select,
textarea {
  color: #222;
}
::-moz-selection {
  background: #b3d4fc;
  text-shadow: none;
}
::selection {
  background: #b3d4fc;
  text-shadow: none;
}
img {
  vertical-align: middle;
}
fieldset {
  border: 0;
  margin: 0;
  padding: 0;
}
textarea {
  resize: vertical;
}
.chromeframe {
