@import url(https://fonts.googleapis.com/css?family=Montserrat:300,400,500,60,700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Roboto:300,400,500&display=swap);
.clearfix::after {
  content: "";
  clear: both;
  display: table;
}
* {
  font-family: 'Montserrat', sans-serif;
  color: #FFFFFF;
  box-sizing: border-box;
}
div.top-nav {
  display: flex;
  padding: 1.4rem;
  background-color: #2a2837;
}
div.top-nav .top-nav-item {
  padding-right: 1.6rem;
  padding-left: 1.6rem;
}
div.top-nav .top-nav-item > a {
  text-decoration: none;
  text-transform: uppercase;
  font-family: 'Montserrat', sans-serif;
  font-weight: 700;
  letter-spacing: 3px;
  font-size: 0.8rem;
}
div.top-nav .top-nav-item > a.Contact {
  border: 2px solid white;
  padding: 0.4rem;
}
@media only screen and (max-width: 900px) {
  div.top-nav {
    padding: 1rem;
    flex-direction: column;
  }
  div.top-nav .top-nav-item {
    padding-right: 0.8rem;
    padding-left: 0.8rem;
    padding-bottom: 0.8rem;
  }
  div.top-nav .top-nav-item > a.Contact {
    border: 1.5px solid white;
    padding: 0.3rem;
  }
}
