@import url(https://fonts.googleapis.com/css?family=Montserrat:300,400,500,60,700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Roboto:300,400,500&display=swap);
.clearfix::after {
  content: "";
  clear: both;
  display: table;
}
* {
  font-family: 'Montserrat', sans-serif;
  color: #FFFFFF;
  box-sizing: border-box;
}
div.main-intro {
  background-color: #12131e;
}
div.main-intro img.image-main {
  max-width: 35%;
}
div.main-intro .main-intro-about {
  width: 65%;
  display: inline-block;
  padding: 2rem;
}
div.main-intro .main-intro-about > img {
  max-width: 30%;
  vertical-align: middle;
  padding: 2rem;
}
div.main-intro .main-intro-about > .about-text {
  display: inline-block;
  vertical-align: middle;
  max-width: 70%;
  padding-left: 2rem;
}
div.main-intro .main-intro-about .about-name {
  font-size: 2.8rem;
  font-family: 'Montserrat', sans-serif;
  font-weight: 700;
  margin-bottom: 1rem;
  text-transform: uppercase;
  letter-spacing: 5px;
  padding-bottom: 1rem;
}
div.main-intro .main-intro-about .about-general,
div.main-intro .main-intro-about .about-description {
  line-height: 1.6rem;
  font-family: 'Roboto', sans-serif;
  letter-spacing: 1px;
  font-size: 1rem;
  font-weight: 400;
  margin-right: 1rem;
}
div.main-intro .main-intro-about .about-description {
  margin-bottom: 1rem;
}
div.main-intro::after {
  content: "";
  clear: both;
  display: table;
}
@media only screen and (max-width: 1300px) {
  div.main-intro .main-intro-about > img {
    max-width: 30%;
    padding: 0;
  }
  div.main-intro .main-intro-about > .about-text {
    max-width: 70%;
  }
  div.main-intro .main-intro-about .about-name {
    font-size: 2.2rem;
  }
  div.main-intro .main-intro-about .about-general,
  div.main-intro .main-intro-about .about-description {
    line-height: 1.2rem;
    font-size: 0.8rem;
  }
}
@media only screen and (max-width: 1000px) {
  div.main-intro img.image-main {
    max-width: 100%;
  }
  div.main-intro .main-intro-about {
    width: 100%;
  }
  div.main-intro .main-intro-about > img {
    padding: 2rem;
  }
}
@media only screen and (max-width: 650px) {
  div.main-intro .main-intro-about {
    padding: 1rem;
  }
  div.main-intro .main-intro-about > img {
    max-width: 100%;
    display: block;
    margin: auto;
    padding: 15%;
  }
  div.main-intro .main-intro-about > .about-text {
    display: block;
    max-width: 100%;
  }
  div.main-intro .main-intro-about .about-name {
    font-size: 1.6rem;
  }
}
