@import url(https://fonts.googleapis.com/css?family=Montserrat:300,400,500,60,700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Roboto:300,400,500&display=swap);
.clearfix::after {
  content: "";
  clear: both;
  display: table;
}
* {
  font-family: 'Montserrat', sans-serif;
  color: #FFFFFF;
  box-sizing: border-box;
}
.projects {
  background-color: #caa1c7;
  padding: 2rem;
  font-family: 'Roboto', sans-serif;
  font-weight: 600;
}
.projects .project-item {
  text-align: center;
  width: calc(100% / 3);
  display: inline-block;
  vertical-align: top;
  padding: 1rem;
  line-height: 1.2rem;
  font-size: 1rem;
}
.projects .project-item .project-title {
  text-transform: uppercase;
  font-family: 'Montserrat', sans-serif;
  font-weight: 700;
  letter-spacing: 2px;
  border: 3px solid #2a2837;
  padding: 0.4rem;
  display: inherit;
  margin-bottom: 1rem;
  color: #2a2837;
}
.projects .project-item > img {
  max-width: 100%;
  display: block;
  margin: auto;
}
.projects .project-item .project-desc {
  color: #2a2837;
  padding-top: 1rem;
  letter-spacing: 0.2px;
}
.projects .project-item .project-link {
  font-weight: 500;
}
.projects .project-item .project-link > a {
  display: block;
  text-decoration: none;
}
.projects .project-item .project-link a:active,
.projects .project-item .project-link a:focus {
  outline: 0;
  border: none;
  outline-style: none;
}
.projects::after {
  content: "";
  clear: both;
  display: table;
}
@media only screen and (max-width: 900px) {
  .projects > .project-item {
    width: 100%;
  }
}
